.feedback__wrapper {
  max-width: 1040px;
  margin: 0 auto;
  padding: 50px 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 1023px) {
  .feedback__wrapper {
    padding: 88px 0 100px;
    display: flex;
    flex-direction: column;
    row-gap: 52px;
  }
}
@media (max-width: 767px) {
  .feedback__wrapper {
    padding: 59px 0 65px;
    row-gap: 35px;
  }
}

.feedback__text-wrap {
  margin-bottom: 6px;
  align-self: center;
}
@media (max-width: 1023px) {
  .feedback__text-wrap {
    margin: 0;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .feedback__text-wrap {
    padding: 0 14px 0;
  }
}

.feedback__heading {
  margin: 0 0 11px;
  font-size: 40px;
  line-height: 1.5;
}
@media (max-width: 1023px) {
  .feedback__heading {
    margin: 0 0 5px;
    font-size: 34px;
  }
}
@media (max-width: 767px) {
  .feedback__heading {
    margin-bottom: 12px;
    font-size: 24px;
    line-height: 1.3;
  }
}

.feedback__text {
  margin: 0;
  font-size: 18px;
  line-height: 1.5;
}
@media (max-width: 767px) {
  .feedback__text {
    font-size: 16px;
    line-height: 1.4;
  }
}

@media (max-width: 767px) {
  .feedback__text br {
    display: none;
  }
}

@media (max-width: 767px) {
  .feedback__text span {
