  }
}

.feedback__form-wrap {
  grid-column: -1/-2;
  justify-self: end;
}

.form {
  width: 403px;
  padding: 45px 38px 50px 50px;
  background-color: #011c40;
  box-shadow: 0px 4px 45px rgba(1, 28, 64, 0.15);
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .form {
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .form {
    width: 100%;
    padding: 34px 17px 40px 29px;
  }
}

.form__fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.form__description {
  margin: 0 0 26px;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
}
@media (max-width: 767px) {
  .form__description {
    margin: 0 0 28px -12px;
    line-height: 1.4;
    text-align: center;
  }
}

.form__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.form__item {
  margin-bottom: 20px;
}
.form__item:last-of-type {
  margin-bottom: 0;
}

.form__item--checkbox {
  margin: -3px 0 27px;
  position: relative;
  padding-left: 23px;
}
@media (max-width: 767px) {
  .form__item--checkbox {
    margin: -2px 0 28px;
    padding-left: 28px;
  }
}

@media (max-width: 767px) {
  .form__item--checkbox label {
    display: flex;
  }
}

@media (min-width: 768px) {
  .form__item--checkbox br {
    display: none;
  }
}

.form__label span {
  display: inline-block;
  font-size: 10px;
  line-height: 1.3;
}
@media (max-width: 767px) {
  .form__label span {
    line-height: 1.2;
  }
}

.form__label input {
  width: calc(100% - 12px);
  padding: 13px 16px 14px;
  border: none;
  background-color: #f9fbfd;
}
.form__label input::placeholder {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  color: rgba(1, 28, 64, 0.7);
}

.form__item--checkbox input {
  margin: auto 0;
  width: 18px;
  height: 18px;
  padding: 0;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  appearance: none;
}
@media (max-width: 767px) {
  .form__item--checkbox input {
    width: 20px;
    height: 20px;
  }
}

.form__item--checkbox svg {
  display: none;
}

.form__item--checkbox input:checked ~ svg {
  display: block;
  position: absolute;
  left: 4px;
  top: 10px;
  bottom: 0;
}
@media (max-width: 767px) {
  .form__item--checkbox input:checked ~ svg {
    left: 5px;
  }
}

