.footer {
  background-color: $color-text;
  color: $color-white-text;
}

.footer__wrapper {
  max-width: 1160px;
  margin: 0 auto;
  padding: 50px 0 40px;

  display: grid;
  grid-template-columns: 70px 1fr;
  column-gap: 40px;

  @include vp-1023 {
    grid-template-columns: 38px 1fr;
    column-gap: 25px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    padding: 40px 0 24px;
  }
}

.footer__logo-wrap {
  margin-bottom: 10px;

  @include vp-767 {
    margin-bottom: 21px;
  }
}

.footer__logo-wrap img {
  @include vp-1023 {
    width: 38px;
    height: 20px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @include vp-767 {
    flex-direction: column;
    align-items: start;
  }
}
